.footer {
	position: relative;
	padding: 40px 0 0;
	@include r(1439) {
		padding-top: 15px; }
	&::before {
		position: absolute;
		top: 0;
		left: 50px;
		right: 50px;
		height: 1px;
		background: $black;
		content: ''; }
	&__container {
		@include clr; }
	&__col {
		float: left;
		width: 25%;
		@include sm {
			width: 50%;
			margin-top: 10px;
			padding: 0 5px; }
		& + & {
			padding-left: 15px;
			@include sm {
				padding-left: 0; } } }
	&__title {
		font-size: 53px;
		@include r(1439) {
			font-size: 35px; }
		@include md {
			font-size: 27px; }
		@include sm {
			font-size: 24px; } }
	&__list {
		margin-top: 25px;
		@include r(1439) {
			margin-top: 15px; }
		li {
			&:not(:last-child) {
				margin-bottom: 10px; }
			a {
				@include sm {
					font-size: 14px; } } }
		&-margin {
			margin-top: 50px;
			@include sm {
				margin-top: 20px; } } } }
