/* some animations
	----------------------------------- */
@keyframes moveDown {
  0% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(4px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes modalComeIn {
  0% {
    visibility: hidden;
    opacity: 0;
    transform: scale(0.8, 0.8);
  }
  65.5% {
    transform: scale(1.03, 1.03);
  }
  100% {
    visibility: visible;
    opacity: 1;
    transform: scale(1, 1);
  }
}

@keyframes modalHeadOut {
  0% {
    visibility: visible;
    opacity: 1;
    transform: translateY(0) scale(1, 1);
  }
  100% {
    visibility: hidden;
    opacity: 0;
    transform: translateY(35px) scale(0.97, 0.97);
  }
}

/* popup
	----------------------------------- */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 15px;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 100;
  text-align: center;
  font-size: 0;
  text-align: center;
  font-size: 0;
}

@media only screen and (max-width: 767px) {
  .popup {
    padding: 10px;
  }
}

.popup:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup.is-open {
  visibility: visible;
  opacity: 1;
}

.popup.is-open .popup__in {
  animation: modalComeIn 0.3s ease;
  visibility: visible;
}

.popup__in {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  max-width: 1088px;
  width: 100%;
  padding: 66px;
  background: #fff;
  box-shadow: 0 0 10px 0 rgba(51, 51, 51, 0.4);
  transform-style: preserve-3d;
  transform: translate3d(0, 0, 0);
  *zoom: 1;
}

.popup__in:after {
  content: " ";
  display: table;
  clear: both;
}

